/**
 *  model
 * @property {string} name ,  model,  combineReducers 
 * @property {object} state 
 * @property {object} reducers reducers ,  reducer  (state, payload) => newState
 */
export const authModel = {
  name: 'auth',
  state: {
    authed: false,
  },
  reducers: {
    login: (state, payload) => {
      return {
        ...state,
        authed: true,
      };
    },

    logout: (state, payload) => {
      return {
        ...state,
        authed: false,
      };
    },
  },
};
