/**
 *  model,  pagination 
 */
export const tableModel = {
  name: 'table',
  state: {
    pagination: {
      allowPagination: false,
      rowsPerPage: 5,
    },
    tableStyle: {
      // 
      compact: false,
    },
  },
  reducers: {
    setAllowPagination: (state, payload) => {
      return {
        ...state,
        pagination: {
          ...state.pagination,
          allowPagination: payload,
        },
      };
    },
    setRowsPerPage: (state, payload) => {
      return {
        ...state,
        pagination: {
          ...state.pagination,
          rowsPerPage: payload,
        },
      };
    },
    setTableStyle: (state, payload) => {
      return {
        ...state,
        tableStyle: {
          ...state.tableStyle,
          compact: payload,
        },
      };
    },
  },
};
