import React, { useState } from 'react';
import { NavBar, Cell, ShareSheet, ImagePreview, Toast } from 'react-vant';
import { useNavigate } from 'react-router-dom';

import websiteQRCode from '../../img/website-qrcode.png';

export default function About() {
  const navigate = useNavigate();
  const [shareSheetVisible, setShareSheetVisible] = useState(false);

  return (
    <div>
      <NavBar safeAreaInsetTop title="" leftArrow={false} />
      <Cell.Group title="">
        <Cell
          title=""
          isLink
          value="Limboer"
          onClick={() => {
            document.location.href = 'https://github.com/hacker0limbo';
          }}
        />
        <Cell
          title=""
          isLink
          value="ikimsam"
          onClick={() => {
            document.location.href = 'http://ikimsam.me';
          }}
        />
      </Cell.Group>

      <Cell.Group title="">
        <Cell
          title=""
          isLink
          onClick={() => {
            document.location.href = 'https://github.com/hacker0limbo/china-top-univ';
          }}
        />
        <Cell
          title=""
          isLink
          onClick={() => {
            navigate('update-logs');
          }}
        />
      </Cell.Group>

      <Cell.Group title="">
        <Cell
          title=""
          isLink
          onClick={() => {
            navigate('disclaimer');
          }}
        />
      </Cell.Group>

      <Cell.Group title="">
        <Cell
          title=""
          isLink
          onClick={() => {
            setShareSheetVisible(true);
          }}
        />
        <Cell
          title=""
          isLink
          onClick={() => {
            navigate('feedback');
          }}
        />

        <ShareSheet
          visible={shareSheetVisible}
          options={[
            { name: '', icon: 'wechat' },
            { name: '', icon: 'link' },
            { name: '', icon: 'qrcode' },
          ]}
          title=""
          onCancel={() => {
            setShareSheetVisible(false);
          }}
          onSelect={(option, index) => {
            // ,  select 
            setShareSheetVisible(false);

            if (option.name === '') {
              ImagePreview.open({
                images: [websiteQRCode],
                closeable: true,
              });
            } else if (option.name === '') {
              const url = 'hacker0limbo.github.io/china-top-univ/';

              if (navigator.clipboard === undefined) {
                //  clipboard API
                //  ios safari  pointDown 
                Toast.info({
                  message: ' clipboard API,  Chrome ',
                });
              } else {
                navigator.clipboard
                  .writeText(url)
                  .then(() => {
                    Toast.success({
                      message: '',
                      duration: 750,
                    });
                  })
                  .catch(() => {
                    Toast.fail({
                      message: ', ',
                    });
                  });
              }
            } else {
              Toast.info({
                message: ', ',
              });
            }
          }}
        />
      </Cell.Group>
    </div>
  );
}
