import React from 'react';
import { NavBar, Tabs, Typography } from 'react-vant';
import { Chart, Interval, Axis, TextGuide, ScrollBar, PieLabel } from '@antv/f2';
import { CHARTS } from '../../constants/store';
import { createUseStyles } from 'react-jss';
import { useSelector } from 'react-redux';
import CanvasImage from './CanvasImage';

import locationData from '../../data/locationData.json';

const useStyles = createUseStyles({
  chartsTitle: {
    marginTop: '12px',
  },
});

export default function Charts() {
  const classes = useStyles();
  const {
    211: location211Data,
    985: location985Data,
    doubleTops: locationDoubleTopsData,
  } = locationData;
  const location985BarChartLayout = useSelector((state) => state.charts.location985.bar.layout);
  const location211BarChartLayout = useSelector((state) => state.charts.location211.bar.layout);
  const locationDoubleTopsBarChartLayout = useSelector(
    (state) => state.charts.locationDoubleTops.bar.layout
  );

  const getLocationBarChartLayoutStateToData = (locationData) => {
    // ,  map
    return {
      [CHARTS.LAYOUT.ASCENDING.STATE]: [...locationData.bar].sort((a, b) => a.count - b.count),
      [CHARTS.LAYOUT.DESCENDING.STATE]: [...locationData.bar].sort((a, b) => b.count - a.count),
      [CHARTS.LAYOUT.DEFAULT.STATE]: [...locationData.bar],
    };
  };

  return (
    <div>
      <NavBar safeAreaInsetTop title="" leftArrow={false} />
      <Tabs active={0} sticky>
        <Tabs.TabPane title="985">
          <Typography.Title center level={4} className={classes.chartsTitle}>
            985
          </Typography.Title>
          <CanvasImage>
            <Chart
              data={
                getLocationBarChartLayoutStateToData(location985Data)[location985BarChartLayout]
              }
              scale={{ count: { max: 10, tickCount: 6 } }}
            >
              <Axis field="location" />
              <Axis field="count" />
              <Interval x="location" y="count" color="location" />
              {location985Data.bar.map((item) => (
                <TextGuide
                  key={item.location}
                  records={[item]}
                  content={`${item.count}`}
                  attrs={{ fill: '#c71a5c', fontSize: '24px' }}
                  offsetY={-10}
                  offsetX={-5}
                />
              ))}
              <ScrollBar mode="x" range={[0, 0.4]} />
            </Chart>
          </CanvasImage>
          <Typography.Title level={4} center className={classes.chartsTitle}>
            985
          </Typography.Title>
          <CanvasImage>
            <Chart
              data={location985Data.pie}
              coord={{
                type: 'polar',
                transposed: true,
                innerRadius: 0.3,
                radius: 0.6,
              }}
            >
              <Interval x="const" y="ratio" adjust="stack" color={{ field: 'location' }} />
              <PieLabel
                label1={(data) => {
                  return {
                    text: `${data.location}: ${(data.ratio * 100).toFixed(2)}%`,
                    fill: '#000000',
                  };
                }}
                label2={(data) => {
                  return {
                    fill: '#000000',
                    text: `${data.count} `,
                    fontWeight: 500,
                    fontSize: 10,
                  };
                }}
              />
            </Chart>
          </CanvasImage>
        </Tabs.TabPane>

        <Tabs.TabPane title="211">
          <Typography.Title level={4} center className={classes.chartsTitle}>
            211
          </Typography.Title>
          <CanvasImage>
            <Chart
              data={
                getLocationBarChartLayoutStateToData(location211Data)[location211BarChartLayout]
              }
              scale={{ count: { tickCount: 6 } }}
            >
              <Axis field="location" />
              <Axis field="count" />
              <Interval x="location" y="count" color="location" />
              {location211Data.bar.map((item) => (
                <TextGuide
                  key={item.location}
                  records={[item]}
                  content={`${item.count}`}
                  attrs={{ fill: '#000', fontSize: '24px' }}
                  offsetY={-10}
                  offsetX={-5}
                />
              ))}
              <ScrollBar mode="x" range={[0, 0.4]} />
            </Chart>
          </CanvasImage>

          <Typography.Title level={4} center className={classes.chartsTitle}>
            211
          </Typography.Title>
          <CanvasImage>
            <Chart
              data={location211Data.pie}
              coord={{
                type: 'polar',
                transposed: true,
                innerRadius: 0.3,
                radius: 0.6,
              }}
            >
              <Interval x="const" y="ratio" adjust="stack" color={{ field: 'location' }} />
              <PieLabel
                label1={(data) => {
                  return {
                    text: `${data.location}: ${(data.ratio * 100).toFixed(2)}%`,
                    fill: '#000000',
                  };
                }}
                label2={(data) => {
                  return {
                    fill: '#000000',
                    text: `${data.count} `,
                    fontWeight: 500,
                    fontSize: 10,
                  };
                }}
              />
            </Chart>
          </CanvasImage>
        </Tabs.TabPane>

        <Tabs.TabPane title="2022">
          <Typography.Title level={4} center className={classes.chartsTitle}>
            2022
          </Typography.Title>
          <CanvasImage>
            <Chart
              data={
                getLocationBarChartLayoutStateToData(locationDoubleTopsData)[
                  locationDoubleTopsBarChartLayout
                ]
              }
              scale={{ count: { max: 40, tickCount: 6 } }}
            >
              <Axis field="location" />
              <Axis field="count" />
              <Interval x="location" y="count" color="location" />
              {locationDoubleTopsData.bar.map((item) => (
                <TextGuide
                  key={item.location}
                  records={[item]}
                  content={`${item.count}`}
                  attrs={{ fill: '#000', fontSize: '24px' }}
                  offsetY={-10}
                  offsetX={-5}
                />
              ))}
              <ScrollBar mode="x" range={[0, 0.4]} />
            </Chart>
          </CanvasImage>

          <Typography.Title level={4} center className={classes.chartsTitle}>
            2022
          </Typography.Title>
          <CanvasImage>
            <Chart
              data={locationDoubleTopsData.pie}
              coord={{
                type: 'polar',
                transposed: true,
                innerRadius: 0.3,
                radius: 0.6,
              }}
            >
              <Interval x="const" y="ratio" adjust="stack" color={{ field: 'location' }} />
              <PieLabel
                label1={(data) => {
                  return {
                    text: `${data.location}: ${(data.ratio * 100).toFixed(2)}%`,
                    fill: '#000000',
                  };
                }}
                label2={(data) => {
                  return {
                    fill: '#000000',
                    text: `${data.count} `,
                    fontWeight: 500,
                    fontSize: 10,
                  };
                }}
              />
            </Chart>
          </CanvasImage>
        </Tabs.TabPane>
      </Tabs>
    </div>
  );
}
